#reviews-widget {
  margin-top: -370px;
  height: 350px;
  width: 100%;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5));
  overflow: auto;
  border-style: solid;
  border-width: 1px;
  box-shadow: 0px 5px 20px black;
  font-family: "Lucida Console", Monaco, monospace;
  clear: both;
  /* padding: 5px; */
  /* margin-bottom: 100px; */
}

 #reviewsTitle {
   font-size: 30px;
  color: rgba(255, 255, 255, 0.75);
  text-align: center;
  font-weight: bolder;
  background: linear-gradient(
    to bottom,
    rgba(255, 0, 0, 0.35),
    rgba(255, 0, 0, 0.35));
    margin-bottom: 5px;
 }
 #authorsName {
  color: darkorange;
  font-weight: bold;
  font-size: 18px;
  margin-left: 5px;
  margin-right: 5px;
  height: 10px;
  margin-top: 10px;
 }
 #authorRating {
  font-size: 20px;
  color: white;
  margin-left: 5px;
  margin-right: 5px;
  font-weight: bold;
  height: 10px;
  width: 30px;
  margin-top: 10px;
  margin-bottom: 15px;
 }
 #authorsTime {
  font-size: 10px;
  color: darkgrey;
  margin-left: 5px;
  margin-right: 5px;
  height: 6px;
  margin-bottom: 5px;
 }
 #authorsReview {
  font-size: 16px;
  color: black;
  font-weight: bold;
  margin-left: 5px;
  margin-right: 5px;
  text-align: center;
  background-color: rgb(236, 227, 227);
}

@media only screen and (max-width: 1200px) {
  #reviews-widget {
    margin-top: -370px;
    margin-left: 10px;
  }
}

@media only screen and (max-width: 991px) {
  #reviews-widget {
    margin-top: -370px;
    margin-left: default;
    /* display: none; */
  }
}

@media only screen and (max-width: 767px) {
  #reviews-widget {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}