#facebook-widget {
  margin-top: 15px;
  height: 1060px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.05);
  border: none;
  overflow: hidden;
  box-shadow: 0px 5px 20px black;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1200px) {
  #facebook-widget {
    margin-left: -50px;
    width: 350px;
  }
}

@media only screen and (max-width: 991px) {
  #facebook-widget {
    display: none;
    /* height: 500px;
    width: 350px;
    margin: auto;
    margin-top: 15px; */
  }
}

@media only screen and (max-width: 767px) {
    #facebook-widget {
      margin: auto;
      margin-top: 20px;
      width: 350px;
    }
}

@media only screen and (max-width: 350px) {
  #facebook-widget {
    width: 300px;
  }
}
  
